// ============================================
// RISIVO CRM - Twilio Subaccounts API
// Manage auto-provisioned Twilio subaccounts
// ============================================

import { NextRequest, NextResponse } from 'next/server'
import { supabaseAdmin as supabase } from '@/lib/supabase/server'
import { twilioService } from '@/lib/telephony/twilio-service'
import type { TwilioSubaccount, AgencyTwilioConfig } from '@/lib/telephony/types'

// ==========================================
// GET - Retrieve subaccount(s)
// ==========================================

export async function GET(request: NextRequest) {
  try {
    const { searchParams } = new URL(request.url)
    const subAccountId = searchParams.get('subAccountId')
    const id = searchParams.get('id')

    if (id) {
      // Get specific subaccount by ID
      const { data, error } = await supabase
        .from('TwilioSubaccount')
        .select('*')
        .eq('id', id)
        .single()

      if (error) throw error

      // Mask sensitive data
      const masked = {
        ...data,
        twilioAuthToken: data.twilioAuthToken ? '••••••••' : null,
      }

      return NextResponse.json({ success: true, data: masked })
    }

    if (subAccountId) {
      // Get subaccount for specific CRM sub-account
      const { data, error } = await supabase
        .from('TwilioSubaccount')
        .select('*')
        .eq('subAccountId', subAccountId)
        .single()

      if (error && error.code !== 'PGRST116') throw error

      if (!data) {
        return NextResponse.json({ success: true, data: null })
      }

      const masked = {
        ...data,
        twilioAuthToken: data.twilioAuthToken ? '••••••••' : null,
      }

      return NextResponse.json({ success: true, data: masked })
    }

    // List all subaccounts (admin only)
    const { data, error } = await supabase
      .from('TwilioSubaccount')
      .select('*')
      .order('createdAt', { ascending: false })

    if (error) throw error

    const maskedData = data.map(d => ({
      ...d,
      twilioAuthToken: d.twilioAuthToken ? '••••••••' : null,
    }))

    return NextResponse.json({ success: true, data: maskedData })
  } catch (error) {
    console.error('Error fetching Twilio subaccount:', error)
    return NextResponse.json(
      { success: false, error: 'Failed to fetch subaccount' },
      { status: 500 }
    )
  }
}

// ==========================================
// POST - Provision new Twilio subaccount
// ==========================================

export async function POST(request: NextRequest) {
  try {
    const body = await request.json()

    if (!body.subAccountId) {
      return NextResponse.json(
        { success: false, error: 'Sub-account ID is required' },
        { status: 400 }
      )
    }

    // Check if subaccount already exists
    const { data: existing } = await supabase
      .from('TwilioSubaccount')
      .select('id')
      .eq('subAccountId', body.subAccountId)
      .single()

    if (existing) {
      return NextResponse.json(
        { success: false, error: 'Twilio subaccount already exists for this sub-account' },
        { status: 409 }
      )
    }

    // Get agency config
    const { data: agencyConfig, error: configError } = await supabase
      .from('AgencyTwilioConfig')
      .select('*')
      .is('agencyId', null) // Get system config
      .single()

    if (configError || !agencyConfig) {
      return NextResponse.json(
        { success: false, error: 'Agency Twilio configuration not found. Please configure master Twilio account first.' },
        { status: 400 }
      )
    }

    // Initialize Twilio service with agency config
    await twilioService.initialize(agencyConfig as AgencyTwilioConfig)

    // Get friendly name from sub-account
    const { data: subAccount } = await supabase
      .from('SubAccount')
      .select('name')
      .eq('id', body.subAccountId)
      .single()

    const friendlyName = body.friendlyName || subAccount?.name || `RISIVO Sub-Account ${body.subAccountId.slice(0, 8)}`

    // Determine webhook base URL
    const webhookBaseUrl = agencyConfig.webhookBaseUrl || process.env.NEXT_PUBLIC_APP_URL || ''

    // Provision the subaccount
    const result = await twilioService.provisionSubaccount(
      {
        subAccountId: body.subAccountId,
        agencyId: body.agencyId || '',
        friendlyName,
        autoProvisionPhoneNumber: body.autoProvisionPhoneNumber ?? agencyConfig.autoProvisionPhoneNumber,
        phoneNumberType: body.phoneNumberType || agencyConfig.defaultPhoneNumberType,
        areaCode: body.areaCode || agencyConfig.defaultPhoneNumberAreaCode,
      },
      webhookBaseUrl
    )

    if (!result.success || !result.subaccount) {
      return NextResponse.json(
        { success: false, error: result.error || 'Failed to provision Twilio subaccount' },
        { status: 500 }
      )
    }

    // Set billing period
    const now = new Date()
    const periodStart = new Date(now.getFullYear(), now.getMonth(), 1)
    const periodEnd = new Date(now.getFullYear(), now.getMonth() + 1, 0)

    // Save subaccount to database
    const subaccountData: Partial<TwilioSubaccount> = {
      ...result.subaccount,
      agencyConfigId: agencyConfig.id,
      currentPeriodStart: periodStart.toISOString(),
      currentPeriodEnd: periodEnd.toISOString(),
    }

    const { data: savedSubaccount, error: saveError } = await supabase
      .from('TwilioSubaccount')
      .insert(subaccountData)
      .select()
      .single()

    if (saveError) throw saveError

    // If phone number was provisioned, save it too
    let savedPhoneNumber = null
    if (result.phoneNumber) {
      const phoneData = {
        ...result.phoneNumber,
        twilioSubaccountId: savedSubaccount.id,
      }

      const { data: phone, error: phoneError } = await supabase
        .from('PhoneNumber')
        .insert(phoneData)
        .select()
        .single()

      if (phoneError) {
        console.error('Error saving phone number:', phoneError)
      } else {
        savedPhoneNumber = phone
      }
    }

    // Mask auth token
    const maskedSubaccount = {
      ...savedSubaccount,
      twilioAuthToken: '••••••••',
    }

    return NextResponse.json({
      success: true,
      data: {
        subaccount: maskedSubaccount,
        phoneNumber: savedPhoneNumber,
      }
    }, { status: 201 })
  } catch (error) {
    console.error('Error provisioning Twilio subaccount:', error)
    return NextResponse.json(
      { success: false, error: 'Failed to provision subaccount' },
      { status: 500 }
    )
  }
}

// ==========================================
// PUT - Update subaccount settings or status
// ==========================================

export async function PUT(request: NextRequest) {
  try {
    const body = await request.json()

    if (!body.id && !body.subAccountId) {
      return NextResponse.json(
        { success: false, error: 'Subaccount ID or sub-account ID is required' },
        { status: 400 }
      )
    }

    // Get existing subaccount
    const query = supabase.from('TwilioSubaccount').select('*')
    if (body.id) {
      query.eq('id', body.id)
    } else {
      query.eq('subAccountId', body.subAccountId)
    }
    
    const { data: existing, error: fetchError } = await query.single()

    if (fetchError || !existing) {
      return NextResponse.json(
        { success: false, error: 'Subaccount not found' },
        { status: 404 }
      )
    }

    // Handle status changes that require Twilio API calls
    if (body.status && body.status !== existing.status) {
      // Get agency config for Twilio API calls
      const { data: agencyConfig } = await supabase
        .from('AgencyTwilioConfig')
        .select('*')
        .is('agencyId', null)
        .single()

      if (agencyConfig && existing.twilioAccountSid) {
        await twilioService.initialize(agencyConfig as AgencyTwilioConfig)

        if (body.status === 'suspended') {
          const result = await twilioService.suspendSubaccount(
            existing.twilioAccountSid,
            body.suspensionReason || 'Suspended by admin'
          )
          if (!result.success) {
            return NextResponse.json(
              { success: false, error: result.error },
              { status: 500 }
            )
          }
        } else if (body.status === 'active' && existing.status === 'suspended') {
          const result = await twilioService.reactivateSubaccount(existing.twilioAccountSid)
          if (!result.success) {
            return NextResponse.json(
              { success: false, error: result.error },
              { status: 500 }
            )
          }
        }
      }
    }

    // Build update object
    const updateData: Record<string, unknown> = {
      updatedAt: new Date().toISOString(),
    }

    // Updateable fields
    const fields = [
      'friendlyName', 'status', 'suspensionReason', 'usageMarkupOverride',
      'dailySmsLimit', 'dailyCallLimit', 'canSendSms', 'canMakeCalls',
      'brandRegistrationId', 'brandRegistrationStatus', 'a2pComplianceStatus'
    ]

    fields.forEach(field => {
      if (body[field] !== undefined) {
        updateData[field] = body[field]
      }
    })

    // Handle suspension timing
    if (body.status === 'suspended' && existing.status !== 'suspended') {
      updateData.suspendedAt = new Date().toISOString()
    } else if (body.status === 'active' && existing.status === 'suspended') {
      updateData.suspendedAt = null
      updateData.suspensionReason = null
    }

    const { data, error } = await supabase
      .from('TwilioSubaccount')
      .update(updateData)
      .eq('id', existing.id)
      .select()
      .single()

    if (error) throw error

    // Mask sensitive data
    const masked = {
      ...data,
      twilioAuthToken: '••••••••',
    }

    return NextResponse.json({ success: true, data: masked })
  } catch (error) {
    console.error('Error updating Twilio subaccount:', error)
    return NextResponse.json(
      { success: false, error: 'Failed to update subaccount' },
      { status: 500 }
    )
  }
}

// ==========================================
// DELETE - Close/remove subaccount (soft delete)
// ==========================================

export async function DELETE(request: NextRequest) {
  try {
    const { searchParams } = new URL(request.url)
    const id = searchParams.get('id')
    const subAccountId = searchParams.get('subAccountId')

    if (!id && !subAccountId) {
      return NextResponse.json(
        { success: false, error: 'Subaccount ID or sub-account ID is required' },
        { status: 400 }
      )
    }

    // Get existing subaccount
    const query = supabase.from('TwilioSubaccount').select('*')
    if (id) {
      query.eq('id', id)
    } else {
      query.eq('subAccountId', subAccountId)
    }
    
    const { data: existing, error: fetchError } = await query.single()

    if (fetchError || !existing) {
      return NextResponse.json(
        { success: false, error: 'Subaccount not found' },
        { status: 404 }
      )
    }

    // Don't actually delete from Twilio - just mark as closed
    // This preserves billing history and allows for recovery

    const { error } = await supabase
      .from('TwilioSubaccount')
      .update({
        status: 'closed',
        canSendSms: false,
        canMakeCalls: false,
        updatedAt: new Date().toISOString(),
      })
      .eq('id', existing.id)

    if (error) throw error

    // Also mark all phone numbers as released
    await supabase
      .from('PhoneNumber')
      .update({
        status: 'released',
        releasedAt: new Date().toISOString(),
        releaseReason: 'Subaccount closed',
        updatedAt: new Date().toISOString(),
      })
      .eq('twilioSubaccountId', existing.id)

    return NextResponse.json({ success: true })
  } catch (error) {
    console.error('Error closing Twilio subaccount:', error)
    return NextResponse.json(
      { success: false, error: 'Failed to close subaccount' },
      { status: 500 }
    )
  }
}
